html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
input:-webkit-autofill {
  background: transparent;
  color: #fff;
  border: 1px solid transparent;
}
*:focus {
  outline: none;
}
.linkStyle {
  color: #2178da;
  cursor: pointer;
}
.linkStyle:hover {
  color: #00a2ff;
}
.btn {
  margin: 0 5px;
  border: 1px solid #98CCE7;
  border-radius: 4px;
  color: #FFF;
  cursor: pointer;
  font: 14px 'Microsoft YaHei', '微软雅黑';
  text-align: center;
  width: 94px;
  height: 34px;
  font-weight: normal;
  word-spacing: 8px;
  letter-spacing: 2px;
}
.btn-sm {
  width: 60px;
  padding: 5px 0px;
}
.btn-md {
  width: 94px;
}
.btn-lg {
  width: 100px;
  padding: 10px 0px;
  font: bold 15px Arial;
}
.btn-default {
  background: #fff;
  border-color: #d1d5de;
  color: #606264;
  height: 30px!important;
}
.btn-default:hover {
  background: #f1f2f4;
  color: #606264;
}
.btn-default:active {
  background: #2178da;
  color: #fff;
  border-color: #2178da;
}
.btn-primary {
  background: #fff;
  border-color: #d1d5de;
  color: #2178da;
  height: 30px!important;
}
.btn-primary:hover {
  background: #f1f2f4;
  color: #2178da;
}
.btn-primary:active {
  background: #2178da;
  color: #fff;
  border-color: #2178da;
}
.btn-warning {
  background: #f0ad4e;
  border-color: #eea236;
}
.btn-warning:hover {
  background: #eea236;
}
.btn-danger {
  background: #d9534f;
  border-color: #d43f3a;
}
.btn-danger:hover {
  background: #d43f3a;
}
.btn-success {
  background: #5cb85c;
  border-color: #4cae4c;
}
.btn-success:hover {
  background: #4cae4c;
}
.btn-info {
  background: #5bc0de;
  border-color: #46b8da;
}
.btn-info:hover {
  background: #46b8da;
}
.btn-disabled {
  background: #eef1f6;
  border-color: #d1d5de;
  color: #bfcbd9;
  height: 30px!important;
}
.btn-disabled:hover {
  cursor: not-allowed;
}
