.icons {
  background: url("assets/public/setup_icon.png") no-repeat;
  width: 22px;
  height: 22px;
  display: block;
}
.setUpRotate {
  background-position: -44px 0px;
}
.setUpOption {
  background-position: -69px 0px;
}
.search {
  background-position: -22px 0px;
}
.setUpUpload {
  background-position: -90px 0px;
}
.setUpDownload {
  background-position: -111px 0px;
}
.setUp {
  float: right;
  margin-left: 20px;
}
.setUp div {
  margin-top: 2px;
  width: 28px !important;
  height: 28px !important;
  float: right;
  background: #fcfcfc;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border: 1px solid #d1d5de;
  border-radius: 3px;
}
.setUp div:hover {
  background-color: #f1f2f4;
}
.setUp div:hover .setUpRotate {
  background-position: -44px 0px;
}
.setUp div:hover .setUpOption {
  background-position: -69px 0px;
}
.setUp div:hover .search {
  background-position: -22px 0px;
}
.setUp div:active {
  background-color: #2178da;
}
.setUp div:active .setUpRotate {
  background-position: -44px -22px;
}
.setUp div:active .setUpOption {
  background-position: -69px -22px;
}
.setUp div:active .search {
  background-position: -22px -22px;
}
.setUp div:nth-child(2) {
  margin-left: 10px !important;
  margin-right: 10px !important;
}
.controlBox div {
  margin-top: 2px !important;
  width: 28px !important;
  height: 28px !important;
}
.controlBox div:nth-child(2) {
  margin-left: 10px !important;
  margin-right: 10px !important;
}
.editSearch {
  font-size: 14px;
}
.el-message-box {
  border-radius: 0px;
}
.el-message-box__btns::before,
.el-message-box__btns::after {
  content: ".";
  clear: both;
  display: block;
  overflow: hidden;
  font-size: 0;
  height: 0;
}
.el-message-box__btns {
  zoom: 1;
}
.el-message-box__btns button {
  float: right;
  margin-left: 15px;
}
.el-message-box__btns button:nth-child(n) {
  background: #fff;
  border-color: #d1d5de;
  color: #606264;
}
.el-message-box__btns button:nth-child(n):hover {
  background: #f1f2f4;
  color: #606264;
}
.el-message-box__btns button:nth-child(n):active {
  background: #2178da;
  color: #fff;
  border-color: #2178da;
}
.el-message-box__btns button:nth-child(2) {
  color: #2178da;
}
.el-input__inner {
  height: 30px;
  border-radius: 0 ;
  border: 1px solid #d1d5de;
}
.el-input__inner:focus {
  border: 1px solid #2178da;
}
.el-textarea__inner {
  border-radius: 0;
  border: 1px solid #d1d5de;
}
.el-textarea__inner:focus {
  border: 1px solid #2178da;
}
.el-select .el-input__inner:focus {
  border: 1px solid #2178da;
}
#left .el-tree-node__content {
  color: #FFFFFF;
  font-size: 12px !important;
  height: 30px;
  line-height: 30px;
  border-radius: 15px;
  padding-left: 8px!important;
  position: relative;
}
#left .el-tree-node__children .el-tree-node__content {
  color: #FFFFFF;
  font-size: 12px !important;
  width: 100%;
  height: 30px;
  line-height: 30px;
  border-radius: 0;
}
#left .el-tree-node__children .el-tree-node__content:hover {
  background: #63758d;
}
#left .el-tree-node__label {
  font-size: 12px;
  padding-left: 5px;
  color: #FFFFFF;
  top: 2px;
  background-position: left!important;
  margin-left: 30px;
  width: 55%;
  position: absolute;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#left .el-date-table td.today {
  color: #e4e8f1;
}
#left .el-date-table td.today:before {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-top: .5em solid #2178DA;
  border-left: .5em solid transparent;
}
#left .el-date-table td.current:not(.disabled),
.el-date-table td.end-date,
.el-date-table td.start-date {
  background-color: #2178DA!important;
}
#left .el-date-table td.today {
  color: #2178DA;
}
#left .el-date-table td.today:before {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-top: .5em solid #2178DA;
  border-left: .5em solid transparent;
}
#left .el-date-table td.current:not(.disabled),
.el-date-table td.end-date,
.el-date-table td.start-date {
  background-color: #2178DA!important;
}
#left .el-pager li {
  color: #67686c;
}
#left .el-pager li:hover {
  color: #67686c;
  background-color: #e4e8f1;
}
#left .el-pager li.active {
  color: #fff;
  background-color: #2178DA;
  border-color: #2178DA;
}
#left .el-tree-node__expand-icon {
  border: none;
  transform: rotate(0);
  width: 85%;
  height: 30px;
  margin: 0;
  position: absolute;
}
#left .el-tree-node__children .el-tree-node__expand-icon:after {
  content: '';
  display: block;
  width: 10px;
  height: 5px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -3px;
  background: url(assets/main/arrow.png) no-repeat;
  background-position: top;
}
#left .el-tree-node__children .expanded:after {
  content: '';
  display: block;
  width: 10px;
  height: 5px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -3px;
  background: url(assets/main/arrow.png) no-repeat;
  background-position: bottom!important;
}
