/*************弹窗公用样式***************/
.alert_box {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 300;
  animation: delayWin .1s;
  -moz-animation: delayWin .1s;
  /* Firefox */
  -webkit-animation: delayWin .1s;
  /* Safari 和 Chrome */
  -o-animation: delayWin .1s;
  /* Opera */
}
.alert_box .maskLayer {
  width: 100%;
  height: 100%;
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0.4;
}
.alert_box .maskUnmber {
  background: #fff;
  position: absolute;
  width: 700px;
  height: 650px;
  min-height: 550px;
}
.alert_box .maskUnmber .hander {
  text-indent: 1em;
  width: 100%;
  height: 8%;
  line-height: 50px;
  text-align: left;
  font-style: normal;
  position: relative;
}
.alert_box .maskUnmber .hander .handerTile {
  font-size: 16px;
  color: #424e59;
}
.alert_box .maskUnmber .hander .closePopup {
  position: absolute;
  display: block;
  width: 50px;
  height: 100%;
  cursor: pointer;
  z-index: 301;
  font-size: 12px;
  right: 0;
  top: 0px;
  padding-right: 15px;
}
.alert_box .maskUnmber .popupContent {
  width: 100%;
  height: 84%;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: left;
  box-sizing: border-box;
  border-top: 1px solid #ced4e4;
  border-bottom: 1px solid #ced4e4;
}
.alert_box .maskUnmber .btn-box {
  height: 8%;
  overflow: hidden;
  position: relative;
  text-align: center;
  box-sizing: border-box;
}
.alert_box .maskUnmber .btn-box .btnBox {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -15px;
  margin-left: -106px;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl {
  width: 94%;
  height: auto;
  overflow: hidden;
  margin-top: 30px;
  margin-left: 28px;
  list-style: none;
  border-top: 1px solid #ced4e4;
  padding-bottom: 30px;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 50%;
  height: 70px;
  float: left;
  border: 1px solid #ced4e4;
  border-top: 0;
  box-sizing: border-box;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li:nth-of-type(odd) {
  border-right: 0;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li .tdTitle {
  font-size: 12px!important;
  color: #a6a8ad;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li .tdContent {
  font-size: 14px!important;
  color: #3b3d40;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li p {
  display: block;
  height: 22px;
  line-height: 22px;
  text-indent: 1em;
  font-size: 14px;
  clear: both;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li p:nth-of-type(1) {
  color: #d2d3d5;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li p:nth-of-type(2) {
  color: #3b3c40;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li p.error {
  color: red;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li p span {
  position: relative;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li p span.redPonit:after {
  content: "*";
  color: red;
  position: absolute;
  right: -10px;
  top: 0px;
  font-size: 20px;
}
.alert_box.alert_normal .maskUnmber .popupContent .popupConUl > li > div {
  margin: 0  12px;
}
.alert_box .clearFix::after,
.alert_box .clearFix::before {
  content: '';
  height: 0;
  width: 0;
  display: block;
  visibility: hidden;
  line-height: 0;
  clear: both;
}
.alert_box .clearFix {
  zoom: 1;
}
.alert_box.alert_setColumn .maskUnmber {
  width: 50%;
  height: 30%;
  min-height: 300px;
}
.alert_box.alert_setColumn .maskUnmber .hander {
  height: 18%;
}
.alert_box.alert_setColumn .maskUnmber .popupContent {
  height: 64%;
}
.alert_box.alert_setColumn .maskUnmber .btn-box {
  height: 18%;
}
.alert_box.alert_table .maskUnmber {
  width: 70%;
  height: 70%;
}
.alert_box.alert_table .maskUnmber .hander {
  height: 8%;
}
.alert_box.alert_table .maskUnmber .popupContent {
  height: 84%;
}
.alert_box.alert_table .maskUnmber .btn-box {
  height: 8%;
}
.alert_box.alert_checkbox .maskUnmber {
  width: 40%;
}
.alert_box.no_submit .maskUnmber .hander {
  height: 8%;
}
.alert_box.no_submit .maskUnmber .popupContent {
  height: 92%;
}
.alert_box.no_submit .maskUnmber .btn-box {
  height: 0;
}
.alert_box.alert_top2 {
  z-index: 400;
}
/*todo ie11兼容暂时处理，居中有所偏差*/
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
.alert_box .maskUnmber {
    top: 18%;
    left: 30%;
}
.alert_box.alert_setColumn .maskUnmber {
    top: 33%;
    left: 25%;
}
.alert_box.alert_table .maskUnmber {
    left: 15%;
}
.alert_box.alert_checkbox .maskUnmber {
    left: 32%;
}
}
@keyframes delayWin {
0% {
    width: 99.99%;
    opacity: 0;
}
100% {
    width: 100%;
    opacity: 1;
}
}
#left .el-checkbox__inner {
  width: 12px;
  height: 12px;
  border-radius: 2px;
}
.stopCar:before {
  display: inline-block;
  content: '';
  width: 6px;
